<script>
import MescrollVue from 'mescroll.js/mescroll.vue'
import { checkBridgeReady, callhandler } from "@/util/bridge";
import { Toast } from 'vant';
import { queryEarnestMoney } from '@/api/request.js'
export default {
  components: { MescrollVue },
  data () {
    return {
      bond: 0,
      pay: 0,
      show: false,
      showExtract: false,
      ExtractNumber: '',
      mescrollUp: {
        callback: this.upCallback,
        htmlNodata: '<p class="upwarp-nodata">-- ' + '暂无数据' + ' --</p>',
        htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">' + '加载中' + '</p>',
        empty: {
          warpId: 'empty',
          icon: require('@/assets/empty.png'),
          tip: '暂无缴纳、提现记录'
        }

      },
      list: []
    }
  },
  mounted () {
    setTimeout(async () => {
      this.Android_showExtract()
      let headerData = await callhandler("getUserToken")
      headerData = {
        token: headerData.split(',')[0],
        userId: headerData.split(',')[1]
      }
      let res = await queryEarnestMoney(headerData)
      let data = res.data.split(',')
      this.bond = data[0]
      this.pay = data[1]
    }, 500)
  },
  methods: {
    // 安卓调用js
    async Android_showExtract () {
      console.log(666, window.WebViewJavascriptBridge)
      // let status = false
